import { useEffect, useState } from "react";
import { SelectEmpresas } from "../Empresas/SelectEmpresas";
import { SelecUsuarios } from "../Usuarios/SelectUsuarios";
import { EnviarBotao } from "./EnviarBotao";
import { FiltroSelect } from "./FiltroSelect";
import { ListaAtividades } from "./ListaAtividades";
import { RecebeDadosInput } from "./RecebeDadosInput";
import { TextInput } from "./TextInput";
import { uid } from "uid";
import { ref, serverTimestamp, set, update } from "firebase/database";
import { db } from "../firebase";

export const Atividades = () => {
  const [id, setId] = useState("");
  const [idedit, setIdEdit] = useState("");
  const [responsavel, setResponsavel] = useState("");
  const [empresa, setEmpresa] = useState("");
  const [atividade, setAtividade] = useState("");
  const [situacao, setSituacao] = useState("");
  const [frequencia, setFrequencia] = useState("");
  const [prazo, setPrazo] = useState("");
  const [realizado, setRealizado] = useState("");
  const [mes, setMes] = useState("");
  const [ano, setAno] = useState("");
  const [editar, setEditar] = useState(false);

  const dadosFreq = [
    "Mensal",
    "Bimestral",
    "Trimestral",
    "Semestral",
    "Anual",
    "Bienal"
  ];
  const dadosRealizado = ["Realizado", "Parcial", "Pendente"];
  const dadosMes = [
    "Janeiro",
    "Fevereiro",
    "Março",
    "Abril",
    "Maio",
    "Junho",
    "Julho",
    "Agosto",
    "Setembro",
    "Outubro",
    "Novembro",
    "Dezembro"
  ];
  const novaData = new Date(prazo);

  useEffect(() => {
    const dadosAno = novaData.getFullYear();
    if (dadosAno) {
      setAno(dadosAno);
    }
  });

  useEffect(() => {
    const pegaMes = dadosMes[novaData.getMonth() + 1];
    if (pegaMes) {
      return setMes(pegaMes);
    }
  });

  useEffect(() => {
    const id = uid();
    setId(id);
  }, []);

  const addAtividade = () => {
    set(ref(db, `atividades/${id}`), {
      id,
      empresa,
      atividade,
      situacao,
      responsavel,
      realizado,
      frequencia,
      prazo,
      mes,
      ano,
      createdAt: serverTimestamp()
    });
  };

  const getData = (item) => {
    setIdEdit(item.id);
    setEmpresa(item.empresa);
    setAtividade(item.atividade);
    setSituacao(item.situacao);
    setResponsavel(item.responsavel);
    setRealizado(item.realizado);
    setMes(item.mes);
    setAno(item.ano);
    setFrequencia(item.frequencia);
    setPrazo(item.prazo);
    setEditar(true);
  };

  const saveEdit = () => {
    update(ref(db, `atividades/${idedit}`), {
      id: idedit,
      empresa,
      atividade,
      realizado,
      situacao,
      responsavel,
      frequencia,
      prazo,
      mes,
      ano
    });
    setEditar(false);
    clearInputs();
  };

  const clearInputs = () => {
    setEmpresa("");
    setAtividade("");
    setSituacao("");
    setResponsavel("");
    setRealizado("");
    setMes("");
    setAno("");
    setFrequencia("");
    setPrazo("");
    setIdEdit("");
    setEditar(false);
  };

  return (
    <div>
      <p>{idedit}</p>
      <h1>Atividades</h1>
      <SelectEmpresas
        value={empresa}
        onChange={(e) => setEmpresa(e.target.value)}
      />
      <TextInput
        id="Atividade"
        value={atividade}
        onChange={(e) => setAtividade(e.target.value)}
      />
      <TextInput
        id="Situaçãp"
        value={situacao}
        onChange={(e) => setSituacao(e.target.value)}
      />
      <SelecUsuarios
        value={responsavel}
        onChange={(e) => setResponsavel(e.target.value)}
      />
      <FiltroSelect
        id="Realizado"
        data={dadosRealizado}
        value={realizado}
        onChange={(e) => setRealizado(e.target.value)}
      />

      <FiltroSelect
        id="Frequência"
        data={dadosFreq}
        value={frequencia}
        onChange={(e) => setFrequencia(e.target.value)}
      />

      <input
        type="date"
        value={prazo}
        onChange={(e) => setPrazo(e.target.value)}
      />
      <RecebeDadosInput value={mes} />
      <RecebeDadosInput value={ano} />
      <EnviarBotao id="Adicionar" onClick={addAtividade} />
      <EnviarBotao id="Editar" onClick={saveEdit} />
      <EnviarBotao id="Cancelar" onClick={clearInputs} />

      <p>{responsavel}</p>
      <p>{mes}</p>
      <p>{id}</p>

      <ListaAtividades onSubmit={getData} />
    </div>
  );
};
