import { onValue, ref } from "firebase/database";
import { useEffect, useState } from "react";
import { SelectEmpresas } from "../Empresas/SelectEmpresas";
import { db } from "../firebase";
import { SelecUsuarios } from "../Usuarios/SelectUsuarios";
import { TabelaAtividades } from "./TabelaAtividades";

export const ListaAtividades = (props) => {
  const [empresa, setEmpresa] = useState("");
  const [responsavel, setResponsavel] = useState("");
  const [lista, setLista] = useState([]);
  const [editarAtividade, setEditarAtividade] = useState("");

  useEffect(() => {
    onValue(ref(db, "atividades"), (snapshot) => {
      setLista([]);
      const data = snapshot.val();
      if (data !== null) {
        Object.values(data).map((lista) => {
          setLista((oldArray) => [...oldArray, lista]);
        });
      }
    });
  }, []);

  console.log(lista);

  const busca = () =>
    lista.filter(
      (item) =>
        item.empresa.includes(empresa) && item.responsavel.includes(responsavel)
    );

  const callback = (item) => {
    setEditarAtividade(item);
    props.onSubmit(item);
  };

  return (
    <div>
      <SelectEmpresas
        value={empresa}
        onChange={(e) => setEmpresa(e.target.value)}
      />
      <SelecUsuarios
        value={responsavel}
        onChange={(e) => setResponsavel(e.target.value)}
      />

      <br />
      <TabelaAtividades data={busca(lista)} callback={callback} />
    </div>
  );
};
